import useStyles from "./styles";
import {
    Card,
    CardActions,
    CardContent,
    CardMedia,
    Button,
    Typography,
    Chip,
    Box,
} from "@mui/material";
import ThumbUpAltIcon from "@material-ui/icons/ThumbUpAlt";
import DeleteIcon from "@material-ui/icons/Delete";
import MoreHorizIcon from "@material-ui/icons/MoreHoriz";
import { useDispatch } from "react-redux";
import { deleteProject, likeProject } from "../../../actions/projects";
type ProjectProps = {
    project: any;
    setCurrentId: (id: number) => void;
    setSelectProjectId: (id: number) => void;
};
const Project = (props: ProjectProps) => {
    const classes = useStyles();
    const dispatch = useDispatch();
    return (
        <Card className={classes.card}>
            <CardMedia
                onClick={() => {
                    props.setSelectProjectId(props.project._id);
                }}
                className={classes.media}
                image={
                    props.project.featuredImage
                        ? `data:image/png;base64, ${props.project.featuredImage}`
                        : "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png"
                }
                title={props.project.title}
            />
            <div
                className={classes.overlay}
                onClick={() => {
                    props.setSelectProjectId(props.project._id);
                }}
            >
                <Typography variant="body2">
                    {"Date Added: "}
                    {new Date(props.project.createdAt).toLocaleDateString()}
                </Typography>
            </div>
            <div className={classes.overlay2}>
                <Button
                    style={{ color: "white" }}
                    size="small"
                    onClick={() => {
                        props.setCurrentId(props.project._id);
                    }}
                >
                    <MoreHorizIcon fontSize="medium" />
                </Button>
            </div>
            <Box className={classes.title} sx={{ my: 1 }}>
                {props.project.tags.map((tag: string) => (
                    <Chip color="secondary" sx={{ mx: 1 }} label={`${tag} `} />
                ))}
            </Box>
            <Typography
                className={classes.title}
                variant="h4"
                component="h2"
                onClick={() => {
                    props.setSelectProjectId(props.project._id);
                }}
            >
                {props.project.title}
            </Typography>
            <Typography
                className={classes.title}
                gutterBottom
                variant="h6"
                component="h2"
                color="textSecondary"
            >
                {"Created by: "}
                {props.project.creator}
            </Typography>
            <Typography
                className={classes.details}
                gutterBottom
                variant="body2"
                color="textSecondary"
                component="p"
                onClick={() => {
                    props.setSelectProjectId(props.project._id);
                }}
            >
                {props.project.description}
            </Typography>
            <CardContent>
                <Typography
                    variant="body2"
                    color="textSecondary"
                    component="p"
                    onClick={() => {
                        props.setSelectProjectId(props.project._id);
                    }}
                >
                    {props.project.message}
                </Typography>
            </CardContent>
            <CardActions className={classes.cardActions}>
                <Button
                    size="small"
                    color="primary"
                    onClick={() => {
                        dispatch(likeProject(props.project._id));
                    }}
                >
                    <ThumbUpAltIcon fontSize="small" /> Like{" "}
                    {props.project.likeCount}{" "}
                </Button>
                <Button
                    size="small"
                    color="primary"
                    onClick={() => {
                        dispatch(deleteProject(props.project._id));
                    }}
                >
                    <DeleteIcon fontSize="small" /> Delete
                </Button>
            </CardActions>
        </Card>
    );
};
export default Project;
