import Project from "./projects/project";
import useStyles from "./styles";
import { useSelector } from "react-redux";
import { Box, CircularProgress, Grid, Typography } from "@mui/material";
import { useState } from "react";
import Single from "../Single/Single";

type ProjectsTypes = {
    setCurrentId: (id: number) => void;
};

const Projects = (props: ProjectsTypes) => {
    const projects = useSelector((state) => (state as any).projects);
    const [selectProjectId, setSelectProjectId] = useState<number | null>(null);
    console.log(projects);
    const classes = useStyles();
    return !projects.length ? (
        <CircularProgress />
    ) : selectProjectId ? (
        <Single
            setSelectProjectId={setSelectProjectId}
            selectedProjectId={selectProjectId}
        ></Single>
    ) : (
        <>
            <Box my={4} display="flex" justifyContent="center">
                <Typography variant="h4" component="h1" gutterBottom>
                    Projects
                </Typography>
            </Box>
            <Grid
                className={classes.mainContainer}
                container
                alignItems="stretch"
                spacing={3}
                sx={{ px: 3 }}
            >
                {console.log("LENGTH", projects.length)}

                {projects.map((project: any) => (
                    <Grid
                        item
                        key={project._id}
                        xs={12}
                        sm={6}
                        sx={{ cursor: "pointer" }}
                    >
                        {console.log(project)}
                        <Project
                            setSelectProjectId={setSelectProjectId}
                            project={project}
                            setCurrentId={props.setCurrentId}
                        ></Project>
                    </Grid>
                ))}
            </Grid>
        </>
    );
};
export default Projects;
