import {
    Box,
    Button,
    Container,
    Grid,
    Paper,
    Chip,
    Typography,
} from "@mui/material";
import { useSelector } from "react-redux";

type SingleProps = {
    selectedProjectId: number | null;
    setSelectProjectId: (id: number | null) => void;
};

const Single = (props: SingleProps) => {
    const selectedProject = useSelector((state) =>
        props.selectedProjectId
            ? (state as any).projects.find(
                  (p: any) => p._id === props.selectedProjectId
              )
            : null
    );
    return (
        <Container>
            <Button
                sx={{ mt: 2, fontSize: "20px" }}
                onClick={() => props.setSelectProjectId(null)}
            >
                {"‚ù¨ Go Back"}
            </Button>
            <Grid
                container
                justifyContent="space-between"
                alignItems="stretch"
                spacing={3}
                sx={{ mb: 2 }}
            >
                <Grid item xs={12} sm={6}>
                    <img
                        src={
                            selectedProject.featuredImage
                                ? `data:image/png;base64, ${selectedProject.featuredImage}`
                                : "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png"
                        }
                        style={{
                            maxHeight: "350px",
                            maxWidth: "350px",
                            height: "auto",
                            width: "auto",
                        }}
                    />
                </Grid>

                <Grid item xs={12} sm={6}>
                    <Box my={4} display="flex">
                        <Typography variant="h4" component="h1" gutterBottom>
                            {selectedProject.title}
                        </Typography>
                    </Box>
                    {selectedProject.tags.map((tag: string) => (
                        <Chip
                            color="secondary"
                            sx={{ mx: 1 }}
                            label={`${tag} `}
                        />
                    ))}
                </Grid>
            </Grid>
            <hr />

            <Box>
                <Typography
                    dangerouslySetInnerHTML={{
                        __html: selectedProject.sanitizedHtml,
                    }}
                ></Typography>
            </Box>
        </Container>
    );
};
export default Single;
